@charset "UTF-8";

.banner {
  display: block;
  width: 100%;
  height: 100vh;
  background: url("../assets/vs—Åc.png") 100%, no-repeat;
  background-size: 100% 100%;
}

.banner__description h1 {
  width: 230px;
  color: white;
  font-family: Impact, sans-serif;
  font-size: 48px;
  text-align: center;
  text-transform: uppercase;
  padding-top: 130px;
  padding-bottom: 50px;
  margin: 0 auto;
}

.banner__choice-button {
  display: block;
  color: whitesmoke;
  font-size: 25px;
  background-image: linear-gradient(#2e0000, #564b1a);
  border: none;
  border-radius: 5px;
  outline: none;
  margin-left: calc(50% - 60px);
}

.banner__choice-button:hover {
  color: #d9dbeb;
  background-image: linear-gradient(#880000, #baab34);
}

@media all and (max-width: 1200px) {
  .banner {
    height: 600px;
  }
}

@media all and (max-width: 768px) {
  .banner {
    height: 400px;
  }

  .banner__description h1 {
    font-size: 25px;
    padding-bottom: 40px;
  }
}

@media all and (max-width: 480px) {
  .banner {
    height: 300px;
  }

  .banner__description h1 {
    padding-top: 95px;
  }
}

.popup-container {
  display: none;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  position: absolute;
  top: .1px;
}

.popup-container__frame {
  width: 100%;
  min-height: 300px;
  border: none;
}

.close {
  font-size: 24px;
  color: white;
  position: relative;
  left: calc(50% + 210px);
  top: 7%;
  cursor: pointer;
}

.close.-left-side {
  position: absolute;
  margin-left: -435px;
}

.widget {
  display: block;
  position: fixed;
  right: 50px;
  bottom: 30px;
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 10px;
  font-family: Lato, sans-serif;
  font-size: 25px;
}

.widget__button {
  color: white;
  margin: 0 auto;
  padding: 8px 15px;
}

.widget__show {
  display: none;
}

.widget__show iframe {
  border: none;
  width: 400px;
  height: 310px;
}

.widget:hover .widget__show {
  display: block;
}

@media all and (max-width: 480px) {
  .widget {
    right: 15px;
    bottom: 15px;
  }
}

@media all and (max-width: 360px) {
  .widget {
    right: 1px;
    width: 300px;
  }

  .widget .close {
    left: calc(50% + 150px);
  }

  .widget .close.-left-side {
    margin-left: -375px;
  }
}

