.popup {
  display: block;
  width: 380px;
  background-color: rgba(255, 255, 255, 0);
  border-radius: 10px;
  margin: 0 auto;
}

.popup__form input {
  outline: none;
  border: none;
  background-color: #b6f5ff;
  height: 25px;
}

.popup__form input:nth-child(2) {
  margin: 0 -4px;
}

.popup__form input:last-child {
  background-color: #55ff83;
  color: #575c18;
}

.popup__social h4 {
  font-family: Arial, sans-serif;
  z-index: 0;
  margin-bottom: 0;
  color: aquamarine;
}

.popup__article {
  width: 378px;
  background: url("../assets/vs-popup.png") 100%, no-repeat;
  background-size: cover;
}

.popup__article h3 {
  font-family: "Arial Black", sans-serif;
  color: #eefff2;
}

.popup__article p {
  font-family: Lato, sans-serif;
  font-weight: 700;
  color: #e0ff23;
}

.popup__social-container {
  display: block;
  padding-left: 0;
  margin: 0 auto;
  background: transparent;
}

.popup__go-to-social {
  display: block;
  width: 378px;
  border-top: 1px solid transparent;
  border-right: 1px solid transparent;
  border-left: 1px solid transparent;
  background-color: red;
  position: absolute;
  opacity: 0;
  text-align: center;
  z-index: -1;
  margin-top: -89px;
  margin-left: -1px;
}

.popup__social-item {
  display: inline-block;
  width: 33%;
  text-align: center;
  border: 1px solid transparent;
  border-top: none;
  border-radius: 0 0 10px 10px;
  background-color: rgba(154, 0, 0, 0.65);
  padding-top: 15px;
  margin: 0;
  z-index: 3;
}

.popup__social-item:nth-child(2) .popup__go-to-social {
  margin-left: -128px;
}

.popup__social-item:last-child .popup__go-to-social {
  margin-left: -253.5px;
}

.popup__social-item p {
  font-family: Lato, sans-serif;
  color: white;
}

.popup__social-item a {
  font-family: Arial, sans-serif;
  text-decoration: none;
  font-size: 20px;
  background-color: #c4c344;
  color: white;
  padding: 7px 15px;
  border-radius: 8px;
}

.popup__social-item a:hover {
  background-image: linear-gradient(#ff2109 23%, #f29a3e 66%, #f2e22f 100%);
}

.popup__social-item:nth-child(2) {
  margin: 0 -3px;
}

.popup__social-item:hover {
  background-color: #9a0203;
  border-color: #c4c344;
}

.popup__social-item:hover .popup__go-to-social {
  background-color: #9a0604;
  border-color: #c4c344;
  opacity: 1;
  z-index: 1;
}

.popup__social-item img {
  width: 32px;
  height: 32px;
  margin-bottom: 15px;
}

.popup__terms {
  font-family: Lato, sans-serif;
  font-size: 10px;
  color: lightgray;
  text-decoration: none;
  padding: 5px 10px;
}

@media all and (max-width: 360px) {
  .popup {
    width: 300px;
    margin: 0;
  }

  .popup__article {
    width: 298px;
  }

  .popup__go-to-social {
    width: 298px;
  }
}

#phone-number {
  height: 25px;
  outline: none;
  border: none;
  background-color: #b6f5ff;
}

.main {
  display: block;
  width: 300px;
  margin: 0 auto;
}

.main__license {
  background-color: #e5e5e5;
  color: black;
  font-family: sans-serif;
}

